/* Fonts: */
@import url(https://fonts.googleapis.com/css2?family=Libre+Franklin:wght@100&family=Merriweather:wght@300&family=Nunito:ital,wght@0,200;1,200&family=Palette+Mosaic&family=Spinnaker&display=swap);
@import url(https://fonts.googleapis.com/css2?family=EB+Garamond:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Serif:wght@100&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Serif:wght@100&display=swap);

$yellow: rgb(254, 211, 1);
$blue: rgb(61, 155, 233);

html {
    scroll-behavior: smooth;
}

h1 {
    font-family: 'Spinnaker', sans-serif;
    font-size: 30px;
    text-transform: uppercase;
}

h2,
h3,
h4,
p,
div,
a {
    font-family: "PT Serif", serif;
    font-weight: bold;
}

section {
    width: 100%;
    min-height: 100vh;
    padding-bottom: 20px;
    scroll-margin-top: 75px;
}

header {
    width: 100%;
    background-color: #3d9be9;
    z-index: 20;
    position: fixed;
    top: 0;
    left: 0;

    ul {
        display: flex;
        list-style: none;
        height: 64px;
        flex-direction: row;
        justify-content: flex-end;
        align-items: center;
        padding-right: 120px;

        li {
            padding: 10px;
            font-family: 'Spinnaker', sans-serif;

            a {
                text-decoration: none;
                color: rgb(61, 61, 61);
                font-family: 'Spinnaker', sans-serif;
                font-weight: normal;
            }

            a:hover {
                color: #5c5454;
            }
        }
    }
}

section#books {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    align-items: center;
    text-align: center;

    .divider-container {
        width: 650px;

        .top-divider {
            border: 1px solid $yellow;
            margin-bottom: 30px;
        }
    }

    .books {
        display: flex;
        flex-direction: row;
        justify-content: center;
        gap: 10%;

        img {
            box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.5);
        }

        hr {
            margin-top: 15px;
            border: 1.5px solid $yellow;
        }

        img,
        hr {
            width: 80%;
        }
        img:hover {
            cursor: pointer;
        }
    }

    .button {
        margin-top: 50px;
        background-color: $yellow;
        padding: 20px 15px;
        text-decoration: none;
        color: black;
    }

    .button:hover {
        background-color: #f2d301;
    }
}

@media (max-width: 700px) {
    section#books {
        .books {
            flex-direction: column;
            gap: 0;

            img,
            hr {
                width: 100%;
            }
        }

        .button {
            width: 70%;
        }
    }
}

section#home {
    background-image: url(../images/homebackground.gif);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;

    div {
        position: absolute;
        top: 60px;
        left: 170px;
        text-shadow: rgb(0 0 0 / 30%) 0 5px 0;
        display: flex;
        flex-direction: column;

        h1 {
            font-size: 84px;
            font-family: "PT Serif", serif;
            text-transform: none;
            color: $blue;
        }

        h4 {
            font-size: 16px;
            color: $blue;
            font-family: "PT Serif", serif;
            margin-top: -40px;
            text-align: center;
        }
    }

    h3 {
        font-size: 14px;
        font-weight: lighter;
        position: absolute;
        top: calc(100vh + 7px);
        right: 20px;
    }

    margin-bottom: 30px;
}

@media (max-width: 800px) {
    section#home {
        display: flex;
        justify-content: center;

        div {
            justify-content: center;
            align-content: center;
            text-align: center;
            position: static;
        }
    }
}

section#inPress {
    padding-top: 20px;
    background-color: $yellow;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;

    hr {
        border: 1px solid white;
        width: 650px;
    }

    .table {
        margin-top: 60px;
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        justify-content: center;
        width: 70%;
        column-gap: 20px;

        .item {
            img {
                width: 40px;
                display: block;
                margin-left: calc(50% - 20px);
            }

            p {
                margin: 10px;
            }

            .quote {
                padding-top: 8px;
                padding-bottom: 8px;
            }

            .light {
                font-weight: 50;
            }

            .line2,
            .line3 {
                font-style: italic;
            }

            display: flex;
            flex-direction: column;
            text-align: center;
        }
    }

}

@media (max-width: 1000px) {
    section#inPress {
        .table {
            grid-template-columns: 1fr 1fr;
            width: 80%;
        }
    }
}

@media (max-width: 700px) {
    section#inPress {
        .table {
            grid-template-columns: 1fr;
            width: 80%;

            .item {
                padding-top: 10px;
                padding-bottom: 10px;
            }
        }
    }
}

section#contact {
    margin-top: 20px !important;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 80%;
    margin: 0 auto;

    h1 {
        text-align: center;
        color: #333;
    }

    form {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        border: 1px solid black;
        border-radius: 5px;
        width: 600px;
        text-align: center;
        input,
        textarea {
            width: 575px;
            padding: 10px;
            border: 1px solid #ddd;
            border-radius: 5px;
            margin-top: 2px;
        }
        textarea {
            min-height: 220px;
            min-width: 575px;
            max-width: 575px;
            max-height: 220px;
        }

        button {
            width: 100px;
            padding: 10px;
            border: none;
            border-radius: 5px;
            background-color: #333;
            color: #fff;
            cursor: pointer;
            margin-bottom: 20px;
            margin-top: 10px;

            &:hover {
                background-color: #555;
            }
        }
    }
}
@media (max-width: 700px) {
    section#contact {
        form {
            width: 90%;
            textarea, input {
                min-width: 90%;
                max-width: 90%;
            }
        }
    }
}

section#news {
    padding-top: 20px;
    h1 {
        text-align: center;
    }
    background-color: #232323;
    color: $yellow;
    font-size: 14px;
    a {
        color: $yellow;
    }
    .newslinktext {
        display: block;
        width: 80%;
        padding-left: 40px;
    }
}

section#bio {
    width: 70%;
    margin: 0 auto;
    padding-top: 20px;
    h1 {
        text-align: center;
    }
    hr {
        color:rgb(61, 61, 61);
        width: 650px;
    }
    .container {
        margin-top: 40px !important;
        display: flex;
        flex-direction: row;
        align-items: center;
        column-gap: 20px;
        img {
            width: 390px;
           }
        h5 {
            margin: 0;
            font-family: "Spinnaker", sans-serif;
            font-weight: 200 !important;
            text-align: justify;
        }
    
    }
    h4 {
        margin-top: -.25px;
        font-size: 14px;
        font-weight: 200;
    }
}

@media (max-width: 1000px) {
    section#bio {
        .container {
            flex-direction: column;
            align-items: center;
            img {
                width: 100%;
                margin-bottom: 30px;
            }
        }
        h4 {
            text-align: right;
            margin-top: -110px;
        }
        hr {
            display: none;
        }
    }
}

section#say-no-to-the-devil, section#odetta {
    
    margin-top: 140px !important;
    .container {
        display: flex;
        flex-direction: row;
        width: 100%;
        justify-content: space-evenly;
        div {
            width: 40%;
        }
        .left {
            width: 331px;
        }
        h1 {
            margin-top: 0;
        }
        p {
            font-family: "Spinnaker", sans-serif !important;
            font-weight: 300;
        }
        .buttons {
            margin-top: 10px;
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            width: 100%;
            a {
                background-color: $yellow;
                color: black;
                text-decoration: none;
                font-family: "Spinnaker", sans-serif;
                padding: 10px;
                display: inline-block;
            };
            a:hover {
                background-color: #f2d301;
            }
        }
    }
}

@media (max-width: 1000px) {
    section#say-no-to-the-devil, section#odetta {

        .container {
            width: 75%;
            flex-direction: column-reverse;
            align-items: center;
            div {
                width: 100%;
            }
            .right {
                padding-left: 40px;
            }
        }
    }
}

footer {
    text-align: center;
    padding: 10px;
}

hr.books-seperator {
    border: 1px solid $yellow;
    width: 90%;
    margin-top: 30px;
    margin-bottom: 30px;
}